#main-container {
  background-color: black;
  border-bottom: 2px solid gray;
  width: 100%;
  height: 70px;
  display: flex;
  position: fixed;
  z-index: 1000;
}
.main-header-button-in-menu {
  width: 100px;
  height: 50px;
  margin-left: 30px;
  font-size: 30px;
  font-family: Georgia, serif;
  color: white;
  background-color: black;
  border: none;
  cursor: pointer;
}
.menu-icon-in-menu {
  width: 50px;
  height: 50px;
  background-image: url("../white_menu.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  border: none;
  cursor: pointer;
}
ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}
.main-header-button {
  margin-top: 10px;
  margin-left: 50px;
  margin-right: 40px;
  width: 100px;
  height: 50px;
  font-size: 30px;
  font-family: Georgia, serif;
  color: white;
  background-color: black;
  border: none;
  cursor: pointer;
}
.user-chat {
  width: 100%;
  height: 100px;
  border-bottom: 2px solid gray;
}
.chats-container {
  color: white;
  margin-top: 70px;
  border-right: 2px solid gray;
  width: 400px;
  height: 100%;
  font-size: large;
  position: fixed;
}
.menu-icon {
  margin-top: 10px;
  margin-left: 20px;
  width: 50px;
  height: 50px;
  background-image: url("../white_menu.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  border: none;
  cursor: pointer;
}
#side-menu {
  position: fixed;
  top: 0;
  left: 0;
  width: 200px;
  height: 100%;
  background-color: black;
  border: 2px solid grey;
  border-radius: 15%;
  color: white;
  padding: 20px;
  z-index: 1003;
}

#side-menu ul {
  list-style: none;
  padding: 0;
}

#side-menu li {
  margin: 10px 0;
}
